import css from './mobileMenu.module.scss'

const MobileMenu = () => {
    return(
        <div className={css.mobileMenuOverlay}>
            <div className={css.mobileMenu}>
                <div className={css.mobileMenuRow}><a href="/about"> </a></div>
                <div className={css.mobileMenuRow}><a href="/pricelist">  </a></div>
                {/* <div className={css.mobileMenuRow}><a href="/discounts"></a></div> */}
                <div className={css.mobileMenuRow}><a href="/contacts"></a></div>
            </div>
        </div>
        
    );

}

export default MobileMenu