import { List, Segment, Header } from 'semantic-ui-react'
import css from './mainAdminPage.module.scss'

const MainAdminPage = () => {
    return(
        <div className={css.mainAdmin}>
            <Segment>
                <Header as="h2" float="left">
                        !
                </Header>
            </Segment>
            <Segment>
                <Header as="h3" float="left">
                      v0.2 beta:
                </Header>
                <Header as="h4" float="left">
                    :
                </Header>
                <List bulleted>
                    <List.Item>  </List.Item>
                    <List.Item>     Eleganto </List.Item>
                    <List.Item>    </List.Item>
                </List>
                <Header as="h4" float="left">
                    :
                </Header>
                <List bulleted>
                    <List.Item> </List.Item>
                    <List.Item>  ,       </List.Item>
                    <List.Item>  ,  changelogs</List.Item>
                </List>
            </Segment>

        </div>
    );
}

export default MainAdminPage;