import { NavLink } from 'react-router-dom'
import css from './headerAdmin.module.scss'

const HeaderAdmin = ({isLogin, onLogout}) => {
    return(
        <div className={css.header}>
            <h2>Eleganto</h2><h4> v0.2 beta</h4>
            <h3 className={css.adminpanelname}>Admin Panel - SmileA</h3>
            {isLogin ? <div className={css.headerMenu}>
                <h4></h4>
                <NavLink to="/adminpage/mainpage" activeClassName={css.selectedLink}></NavLink>
                <NavLink to="/adminpage/start" activeClassName={css.selectedLink}> </NavLink>
                <NavLink to="/adminpage/manual" activeClassName={css.selectedLink}>  </NavLink>
            </div> : null }
            {isLogin ? <div className={css.headerMenu}>
                <h4> </h4>    
                <NavLink to="/adminpage/services" activeClassName={css.selectedLink}> </NavLink>
                <NavLink to="/adminpage/discounts" activeClassName={css.selectedLink}> </NavLink>
                <NavLink to="/adminpage/forms" activeClassName={css.selectedLink}> </NavLink>
            </div> : null}
            {isLogin ? <div className={css.headerMenu}>
                <h4>: </h4>
                <NavLink to="/adminpage" onClick={() => onLogout()}></NavLink>
            </div> : null}
            
            
        </div>
    );
}

export default HeaderAdmin;