import css from './main.module.scss'

const MainBanner = () => {
    return(
        <div className={css.mainBanner}>
            <div className={css.mainBox}>
                <div className={css.mainBoxWords}>
                    <div className={css.mainBoxWordsOne}>  -  !</div>
                    <div className={css.mainBoxWordsTwo}>,    -</div>
                </div>
                <div className={css.mainBoxImage}></div>
            </div>
        </div>
        
    );
}

export default MainBanner;